import { useParams } from 'react-router-dom';
import collageapi from 'api/collage';
import helpers from 'components/collage/helpers';
import BaseCollage from 'pages/collage/Base';

async function resizeCollageImages(
  imageArr,
  resizeWidth,
  handleImageCount,
  handleNewImageCount,
  handleImageDetails
) {
  console.log('images array', imageArr);
  handleImageCount(imageArr.length);
  handleNewImageCount(imageArr.length);
  const imgs = await Promise.allSettled(
    imageArr.map(async (imageElem) => {
      const result = new Promise((resolve) => {
        const image = new Image();
        image.onload = (e) => {
          const { naturalWidth, naturalHeight } = e.target;
          const aspectRatio = naturalWidth / naturalHeight;
          const { title, type } = imageElem;
          const dataurl = helpers.downscaleImage(
            image,
            resizeWidth,
            resizeWidth / aspectRatio,
            type
          );
          const resizedImageDet = {
            title,
            src: dataurl,
            naturalHeight: resizeWidth / aspectRatio,
            naturalWidth: resizeWidth,
            aspectRatio,
            type,
          };
          resolve(resizedImageDet);
          handleImageDetails(resizedImageDet, imageElem);
        };
        image.src = imageElem.src;
      });
      return result;
    })
  );
  return imgs.map((elem) => elem.value);
}

export default function CollageView() {
  const params = useParams();
  const { collageID } = params;

  const getCollage = async (
    resizeImageWidth,
    handleImageCount,
    handleNewImageCount,
    handleImageDetails
  ) => {
    console.log('use effect making call to get collage');
    const details = await collageapi.getCollage(collageID);
    const collDets = details.details;
    const tempImages = await Promise.allSettled(
      collDets.images.map(async (img) => {
        const src = details.urls[img.title];
        const imageSRC = await fetch(src).then((resp) => resp.text());
        console.log('image SRC', imageSRC);
        return { ...img, src: imageSRC };
      })
    );
    const originalImages = tempImages.map((elem) => elem.value);
    const images = await resizeCollageImages(
      originalImages,
      resizeImageWidth,
      handleImageCount,
      handleNewImageCount,
      handleImageDetails
    );
    console.log('temp images', tempImages, originalImages, images);
    return { details: collDets, images, originalImages };
  };

  return (
    <BaseCollage
      getCollage={getCollage}
      clearCollage={() => {}}
      saveType="existing"
    />
  );
}
